import Image from "next/image";
import React from "react";
import Button from "../Button/button";
import Vector from "@/assets/svg/vector";

import {
  CardContainer,
  ImageContainer,
  TimeArea,
  Line,
  LineContainer,
  VectorContainer,
  Container,
  Dot,
} from "./PostCard.styles";
import { Body2, Body3 } from "../Typography/Body.styles";
import { H1 } from "../Typography/Heading.styles";

interface PostCardProps {
  index: number;
  post: any;
}

const PostCard = ({ index, post }: PostCardProps) => {
  const formatDate = (postDate: string) => {
    const date = new Date(postDate);

    const formatted = date.toLocaleDateString("de-DE");
    return formatted;
  };
  return (
    <CardContainer>
      <TimeArea>
        <Container $flex={3}>
          <LineContainer>
            <Line></Line>
            <VectorContainer $left={false}>
              <Vector />
            </VectorContainer>
          </LineContainer>
          <Dot></Dot>
        </Container>
        <Body3>{formatDate(post.publishedAt)}</Body3>
        <Container $flex={3}>
          <Dot></Dot>
          <LineContainer>
            <VectorContainer $left={true}>
              <Vector />
            </VectorContainer>
            <Line></Line>
          </LineContainer>
        </Container>
      </TimeArea>
      <H1>{post.title}</H1>
      <ImageContainer>
        <Image
          className="w-full h-[400px] rounded-xl"
          src={`http://localhost:1337${post.cover.url}`}
          alt="image"
          objectFit="cover"
          fill
        />
      </ImageContainer>
      <Body2>{post.mainContent}</Body2>
      <Button>Xem thêm</Button>
    </CardContainer>
  );
};

export default PostCard;
