"use client";
import React, { useEffect, useState } from "react";
import { Container, Field } from "./InfoCard.styles";
import { H2 } from "@/components/Typography/Heading.styles";
import { Body3 } from "@/components/Typography/Body.styles";
import Image from "next/image";

import FacebookIC from "@/assets/svg/fb";
import TwitterIC from "@/assets/svg/x";
import IGIC from "@/assets/svg/ig";
import LinkedinIC from "@/assets/svg/linkedin";
import TextDecor from "@/assets/svg/textArea";

interface InfoProps {
  fullname: string;
  biography: string;
  contact: ContactProps[];
  interest: InterestProps[];
  avatar: AvatarProps;
}

interface ContactProps {
  id: number;
  platform: string;
  url: string;
}

interface InterestProps {
  id: number;
  interest: string;
}

interface AvatarProps {
  id: string;
  url: string;
  name: string;
  formats?: {
    thumbnail?: { url: string };
    small?: { url: string };
    medium?: { url: string };
    large?: { url: string };
  };
}

const InfoCard = () => {
  const [info, setInfo] = useState<InfoProps[] | null>(null);
  const [loading, setLoading] = useState(false);
  const handleGetInfo = async () => {
    setLoading(true);
    try {
      const response = await fetch(
        "http://localhost:1337/api/authors?populate=*",
        { method: "GET" }
      );

      const data = await response.json();

      if (!response.ok) {
        setLoading(false);
      }

      setInfo(data.data);
      console.log(data.data);
    } catch (error) {
      console.log("Error: " + error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    handleGetInfo();
  }, []);

  return (
    <Container>
      {info?.map((item, index) => (
        <Container key={index}>
          <Image
            className=" rounded-full"
            src={`http://localhost:1337${item.avatar.url}`}
            alt="image"
            objectFit="cover"
            width={220}
            height={220}
          />
          <H2>my {item.fullname} diary</H2>
          <Field>
            {item.interest.map((item, index) => (
              <Body3 key={index}>{item.interest}</Body3>
            ))}
          </Field>
          <div className="h-1.5"></div>
          <Body3 $align="center">{item.biography}</Body3>
          <Field>
            {item.contact.map((item, index) => (
              <a href={item.url} key={index}>
                {item.platform === "fb" ? (
                  <FacebookIC />
                ) : item.platform == "ig" ? (
                  <IGIC />
                ) : item.platform === "x" ? (
                  <TwitterIC />
                ) : (
                  <LinkedinIC />
                )}
              </a>
            ))}
          </Field>
        </Container>
      ))}
    </Container>
  );
};

export default InfoCard;
